<template>
    <form @submit.prevent="handleSubmit">
        <label>Email:</label>
        <input type="email" required v-model="email">
        
        <label>Password:</label>
        <input type="password" required v-model="password">
        <div v-if="pwderror">{{ pwderror }}</div>
        <label>Role:</label>
        <select v-model="role">
            <option value="developer">web dev</option>
            <option value="designer">web des</option>
        </select>
        <label>Skills:</label>
        <input type="text" v-model="tempSkill" @keyup="addSkill">
        <div class="terms">
            <input type="checkbox" v-model="terms" required>
            <label >Accept Terms And Conditons</label>
        </div>
        <div v-for="skill in skills" :key="skill" class="pill" @click="deleteSkill(skill)">
            <span>{{skill}}</span>
        </div>
        
        <!-- <div><input type="checkbox" value="AA" v-model="names">
        <label>AA</label></div>
        <div><input type="checkbox" value="vv" v-model="names">
        <label>vv</label></div>
        <div><input type="checkbox" value="xx" v-model="names">
        <label>cc</label></div> -->
        <div class="submit">
            <button>Create An account</button>
        </div>
    </form>
    <div>{{ email }} - {{ password }} - {{role}} - {{ terms }} - {{ skills }}</div>
</template>

<script>
export default {
    data(){
        return{
            email : 'na',
            password : '',
            role : 'designer',
            terms : null,
            // names : [],
            tempSkill : '',
            skills : [],
            pwderror : ''
        }
    },
    methods : {
        addSkill(e){
            
            if (e.code === 'Comma' && this.tempSkill ){ //or e.key === ','
            let tempThing = this.tempSkill.replace(/,/g,"").trim();
            if(!this.skills.includes(tempThing)){

                this.skills.push(tempThing)
            }                
                this.tempSkill = ''
            }
        },
        deleteSkill(thiss){
            
            // if(this.skills.includes(thiss)){
            //     let tempIndex = this.skills.indexOf(thiss)                
            //     this.skills.splice(tempIndex,1)
            // }
            // my way 

            this.skills = this.skills.filter((item)=>{
                return thiss !== item;
            })
        },
        handleSubmit(){
            this.pwderror = this.password.length >= 5 ? '' : 'too short, must be at least 5 more'
        }
    }
}
</script>

<style>
    form{
        max-width: 420px;
        margin:30px auto;
        background: white;
        text-align: center;
        padding: 40px;
        border-radius: 10px;
    }
    label{
        color: #aaa;
        display: inline-block;
        margin: 25px 0 15px;
        font-size: 0.6em;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: bold;
    }
    input, select{
        display: block;
        padding: 10px 6px;
        width: 100%;
        box-sizing: border-box;
        border: none;
        border-bottom: 1px solid #ddd;
        color: #555;

    }
    input[type="checkbox"]{
        display: inline-block;
        width: 16px;
        margin: 0 10px 0 0;
        position: relative;
        top: 2px;
    }
    .pill{
        display: inline-block;
        border: #aaa 1px solid;
        border-radius: 5px;
        background-color: aliceblue;
        margin-right: 3px;
        padding : 3px 6px;
    }
    button{
        background: #0b6dff;
        border: 0;
        padding: 10px 20px;
        margin-top: 20px;
        color: white;
        border-radius: 20px;
    }
    .submit{
        text-align: center;
    }
</style>